"use client"

import { useState } from "react"
import { useSession } from "next-auth/react"
import { Button } from "@/components/ui/button"
import { useRouter } from "next/navigation"

interface ApplyButtonProps {
  jobId: string
}

export function ApplyButton({ jobId }: ApplyButtonProps) {
  const { data: session, status } = useSession()
  const [isApplying, setIsApplying] = useState(false)
  const [hasApplied, setHasApplied] = useState(false)
  const router = useRouter()

  const handleApply = async () => {
    if (!session) {
      router.push('/api/auth/signin')
      return
    }

    setIsApplying(true)
    try {
      const response = await fetch('/api/applications', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ jobId }),
      })

      if (response.ok) {
        setHasApplied(true)
      } else {
        const error = await response.json()
        alert(error.message || 'Failed to apply for job')
      }
    } catch (error) {
      console.error('Error applying for job:', error)
      alert('Failed to apply for job')
    } finally {
      setIsApplying(false)
    }
  }

  if (status === "loading") {
    return (
      <Button disabled className="w-full lg:w-auto">
        Loading...
      </Button>
    )
  }

  if (!session) {
    return (
      <Button onClick={handleApply} className="w-full lg:w-auto">
        Sign In to Apply
      </Button>
    )
  }

  if (hasApplied) {
    return (
      <Button disabled variant="outline" className="w-full lg:w-auto">
        Applied âœ“
      </Button>
    )
  }

  return (
    <Button 
      onClick={handleApply} 
      disabled={isApplying}
      className="w-full lg:w-auto"
    >
      {isApplying ? 'Applying...' : 'Apply Now'}
    </Button>
  )
}
